@import './../common/styles/sass/variables';
@import './../common/styles/sass/mixins';
@import './../common/components/button/Button.module';

.contactBlock {
  height: 100%;
  background-color: $bgColor;
  padding-top: 40px;

  .contactContainer {
    @include container();
    @include flexBox(column, flex-start, center, nowrap);
    background-color: $bgSecondColor;
    padding-top: 60px;
    padding-bottom: 0;


    .contactForm {
      @include flexBox(column, space-between, center, nowrap);
      width: 70%;
      height: 450px;

      @media only screen and (max-width: 1150px) {
        width: 85%;
      }

      .contactInput {
        width: 100%;
        height: 50px;
        @include text($fontFamilyStyle, 16px, 500, $textColor);
        @include inputAndTextArea($inputTextColor, none, 5px, .2s);
        padding: 5px 15px;

        &:focus {
          border: 3px solid $primaryColor;
        }
      }

      .contactTextArea {
        width: 100%;
        height: 200px;
        @include text($fontFamilyStyle, 16px, 500, $textColor);
        @include inputAndTextArea($inputTextColor, none, 5px, .2s);
        padding: 8px 15px;

        &:focus {
          border: 3px solid $primaryColor;
        }
      }

      .contactButton {
        @extend .button;
      }
    }
  }
}
