@import './../../common/styles/sass/mixins';
@import './../../common/styles/sass/variables';

.skill {
  @include flexBox(column, stretch, center, nowrap);
  width: 100%;
  background-color: $bgSecondColor;
  border-radius: 5px;
  @media only screen and (max-width: 1100px) {
    width: 100%;
  }

  .skillIcon {
    width: 100px;
    height: 100px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-top: 20px;
  }

  .skillInfo {
    @include flexBox(column, center, center, nowrap);
    padding: 20px;

    .nameSkillText {
      @include text($fontFamilyStyle, 20px, 600, $textColor);
      margin: 0 0 8px;
    }

    .descriptionSkillText {
      @include text($fontFamilyStyle, 16px, 600, $textColor);
      text-align: center;
    }
  }
}
