@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.button {
  color: $textColor;
  @include flexBox(row, center, center, nowrap);
  @include button(140px, 40px, transparent, $primaryColor);

  &:focus {
    outline: none;
  }

  &:hover {
    background-color: $primaryColor;
    cursor: pointer;
  }
}