@import './src/common/styles/sass/variables';
@import './src/common/styles/sass/mixins';

.headerBlock {
  height: 60px;

  .headerContainer {
    @include flexBox(column, center, center, wrap);
    width: 80%;
    height: 100%;
    margin: 0 auto;

    .header {
      @include flexBox(row, flex-end, center, nowrap);
      position: fixed;
      left: 0;
      z-index: 9999;
      width: 100%;
      height: 60px;
      background-color: $headerAndFooterColor;
    }
  }
}